import { reqGetCode ,reqUserRegidter,reqUserLogin,reqUserInfo,reqLogout} from "@/api";
import {getToken, setToken,removeToken} from "@/utils/token"
// 登录与注册的模块
const state = {
    code:'',
    token:getToken(),
    userInfoff:{}
};
const mutations = {
    GETCODE(state,code){
        state.code = code;
    },
    USERLOGIN(state,token){
        state.token = token;
    },
    GETUSERINFO(state,userInfoff){
        state.userInfoff = userInfoff;
    },
    // 清除本地数据
    CLEAR(state){
        // 把仓库中相关的用户信息清空
        state.token = '';
        state.userInfoff = {};
        // 本地存储数据清空
        removeToken();
    }
};
const actions = {
    // 获取验证码
    async getCode({commit},phone){
        // 获取验证码的这个接口:把验证码返回，但是正常情况，后台把验证码发到用户手机上【省钱】
        let result = await reqGetCode(phone);
        if (result.code == 200) {
            commit('GETCODE',result.data);
        }
    },
    // 用户注册
    async userRegister({commit},user){
    let result = await reqUserRegidter(user);
    if (result.code == 200) {
        return 'ok';
    }else{
        return Promise.reject(new Error('faile'));
    }
    },
    //用户登录
    async userLogin({commit},user){
        let result = await reqUserLogin(user);
        if (result.code == 200) {
            // 用户已经登录成功且获取到token
            commit('USERLOGIN',result.data.token);
            // 持久化存储token
            setToken(result.data.token);
            return 'ok';
        }else{
            return Promise.reject(new Error('Faile'));
        }
    },
    // 获取用户信息
    async getUserInfo({commit}){
        let result = await reqUserInfo();
        if (result.code == 200) {
            commit('GETUSERINFO',result.data);
            return 'ok';
        }else{
            return Promise.reject(new Error('faile'));
        }
    },
    // 退出登录
    async userLogout({commit}){
        // 只是向服务器发起一次请求，通知服务器清除token
        let result = await reqLogout();
        if (result.code == 200) {
            //action里面不能操作state，提交mutation修改state
            commit("CLEAR");
            return 'ok';
        }else{
            return Promise.reject(new Error('faile'));
        }
    }
};
const getters = {};
export default{
    actions,
    mutations,
    state,
    getters
}